.photo:nth-child(2){
    grid-column: 2/4;
    grid-row: span 2;
}


@media (max-width: 40rem){
    .photo:nth-child(2){
        grid-column: initial;
        grid-row: initial;
    }   
}

.photo{
    display: grid;
    border-radius: 0.2rem;
    overflow: hidden;
    cursor: pointer;
}

.photo > div{
    grid-area: 1/1;
}

.photo .visualizacao{
    background: rgba(94, 94, 94, 0.3);
    grid-area: 1/1;
    color: white;
    text-shadow: 0 0 1px black;
    font-size: 1rem;
    text-align: center;
    align-items: center;
    justify-content: center;
    display: none;
}

.visualizacao::before{
    content: '';
    width: 16px;
    height: 10px;
    display: inline-block;
    margin-right: .25rem;
    background: url('../../Assets/visualizacao.svg') no-repeat;
}

.photo:hover .visualizacao{
    display: flex;
}








