/*Import de 'Spectral'*/
@import url('https://fonts.googleapis.com/css2?family=Spectral:wght@200&display=swap');
/*Import de 'Spectral'*/

body{
    margin: 0;
    color: #333;    
    --type-first: Helvetica, Arial, sans-serif;
    --type-second: 'Spectral', georgia;
    font-family: var(--type-first);
    padding-top: 4rem;
}

*{
    box-sizing: border-box;
}

h1, h2, h3, h4, p{
    margin: 0px;
}

ul, li{
    list-style: none;
    padding: 0;
    margin: 0;
}

img{
    display: block;
    max-width: 100%;
}

a{
    text-decoration: none;
    color: #333;
}


button{
    cursor: pointer;
}
button,input{
    display: block;
    font-size: 1rem;
    font-family: var(--type-first);
    color: #333;
}

.container{
    max-width: 50rem;
    padding: 0 1rem;
    margin: 0 auto;
}

.mainContainer{
    margin-top: 2rem;
}

.title{
    font-family: var(--type-second);
    line-height: 1;
    font-size: 3rem;
    margin: 1rem 0;
    position: relative;
    z-index: 1;
}



.title::after{
    content: '';
    display: block;
    width: 1.5rem;
    height: 1.5rem;
    background-color: #fb1;
    position: absolute;
    bottom: 5px;
    left: -5px;
    border-radius: .2rem;
    z-index: -1;
}

.animeLeft{
    opacity: 0;
    transform: translateX(-50px);
    animation: animeleft forwards .3s;
}

@keyframes animeleft{
    to{
        transform: translateX(0);
        opacity: 1;
    }
}

.App{
    display: flex;
    flex-direction: column;
    min-height: calc(100vh + 10rem);
}

.AppBody{
    flex: 1;
}

.VictoryContainer{
    height: initial  !important;   
}