.form{
    margin-bottom: 2rem;
}

.perdeu{
    display: inline-block;
    color: #666;
    padding: 1.5rem 0;
    line-height: 1;
}
.perdeu::after{
    content: '';
    display: block;
    height: 2px;
    background: currentColor;
    width: 0;
    margin: 0 auto;
    animation: animax 1.3s forwards;
}
@keyframes animax{
    to{
        width: 100%;
    }
}

.perdeu::before{
    content: '';
    display: block;
    height: 2px;
    width: 2px;
    background-color: #fb1;
    width: 0;
    position: relative;
    top: 18px;
    margin: 0 auto;
    transition: .2s;
}

.perdeu:hover:before{
   width: 100%;
}


.cadastro{
    margin-top: 4rem;
}

.cadastro p{
    margin-top: 2rem;
    margin-bottom: 4rem;
}

.subtitle{
    font-family: var(--type-second);
    line-height: 1;
    font-size: 2rem;
}

.subtitle::after{
    content: '';
    display: block;
    background-color: #ddd;
    height: 0.5rem;
    width: 3rem;
    border-radius: 2px;
}