.photo{
    margin: auto;
    height: 36rem;
    border-radius: .2rem;
    background-color: white;   
    display: grid;
    grid-template-columns: 36rem 20rem;
    grid-template-rows: auto 1fr auto;
    overflow: hidden;
    opacity: 0;
    transform: scale(.8);
    animation: scaleUp .3s forwards;
}


.single.photo{
    display: grid;
    grid-template-columns: 1fr;
    height: auto;
}

@keyframes scaleUp{
    to{
        opacity: initial;
        transform: initial;
    }
}



.details{
    padding: 2rem 2rem .5rem 2rem;
}

.single.photo .details{
    padding:0px 0;
    padding-top: 2rem;
}

.img{
    grid-row: 1/4;
}

.single.photo .img{
    grid-row: 1;
    border-radius: .4rem;
    overflow: hidden;
}


@media (max-width: 64rem){
    .photo{
        height: auto;
        max-height: calc(100vh - 4rem);
        overflow-y: auto;
        grid-template-columns: minmax(20rem, 40rem);
    }
    .img{
        grid-row: 1;
    }

}


.author{
    opacity: 0.6;
    margin-bottom: 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.author a:hover{
    text-decoration: underline;
    color: rgb(9, 13, 129);
}

.vizualizacoes::before{
    content: '';
    width: 16px;
    display: inline-block;
    height: 10px;
    margin-right: 5px;
    background: url('../../Assets/visualizacao-black.svg');
}


.attributes{
    display: flex;
    font-size: 1.125rem;
    font-weight: bold;
    margin-top: 1rem;
    margin-bottom: 2rem;
}

.attributes li {
    margin-right: 2rem;
    font-size: 1.125rem;
}

.attributes li::before {
    content: '';
    width: 2px;
    height: 20px;
    background: #323232;
    display: inline-block;
    position: relative;
    top: 4px;
    right: .5rem;
}

.h2Link{
    font-family: var(--type-second);
    line-height: 1;
    font-size: 3rem;
    margin: 1rem 0;
    position: relative;
    z-index: 1;
}

.h2Link::after{
    content: '';
    display: block;
    width: 1.5rem;
    height: 1.5rem;
    background-color: #fb1;
    position: absolute;
    bottom: 5px;
    left: -5px;
    border-radius: .2rem;
    z-index: -1;
}


.h2Link:hover{
    color: rgb(33, 10, 54);
}
.h2Link:before{
    content: '';
    display: block;
    width: 0;
    height:2px;
    background-color: #fb1;
    position: absolute;
    left: 55%;
    top: 55px;
    opacity: 1;
    z-index: -8px;
}

.h2Link:hover::before{
    z-index: -8px;
    animation: antes forwards .3s;
}

@keyframes antes{
    to{
        opacity: 1;
        width: 55%;
    }
}
