.delete{
    background: #ddd;
    padding: .5rem 2rem;
    line-height: 1;
    border: solid 1px transparent;
    font-size: .875rem;
    font-family: var(--type-first);
    cursor: pointer;
    border-radius: .4rem;
    transition: .3s;
    color: rgb(238, 0, 0);
}

.delete:hover, 
.delete:focus{
    background: rgb(238, 0, 0);
    color: white;
    outline: rgb(238, 0, 0);
}


.delete:disabled{
    cursor: progress;
}