.wrapper{
    margin-bottom: 1rem;
}

.input{
    border: 1px solid #eee;
    display: block;
    width: 100%;
    font-size: 1rem;
    padding: 0.8rem;
    border-radius: 0.2rem;
    background-color: #eee;
    transition: .3s;
}

.input:focus,
.input:hover{
    outline: none;
    border-color: #fb1;
    background-color: white;
    box-shadow: 0 0 1px 3px #fea;
}

.label{
    display: block;
    font-size: 1rem;
    line-height: 1;
    padding-bottom: .5rem;
}


.error{
    color: #f31;
    font-size: 0.87rem;
    margin-top: .25rem;
}