.comments{
    overflow-y: auto;
    word-break: break-all;
    transition: .4s;
    width: 100%;
    margin: 0 auto;
    border-radius: 4px;
    margin-bottom: 4px;
    padding:0 5px;
}


.comments li{
    margin-bottom: 0.5rem;
    line-height: 1.2;
}

.wraperComents{
    padding: 5px 18px;
    padding-top: 10px;
    background: rgb(238, 245, 245);
}


.single.comments{
    padding: 0;
}

.single.comments .wraperComents{ 
    padding: 5px 8px;
}
