.modal{
    position:fixed;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    background-color: rgba(0,0,0, .4);
    display: flex;
    z-index: 1000;
    padding: 2rem calc(4rem + 10px) 2rem 4rem;
}


@media (max-width: 40rem){
    .modal{
        padding: 2rem calc(2rem + 10px) 2rem 2rem;
    }
}


