.nav{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 1rem;
}

.nav a, .nav button{
    background-color: #eee;
    border-radius: .2rem;
    height: 40px;
    width: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: solid 1px transparent;
    transition: .1s;
    cursor: pointer;
}


.nav a:hover,
.nav a:focus,
.nav button:hover,
.nav button:focus{
    background-color: white;
    box-shadow: 0 0 0 3px #eee;
    border-color: #333;
    outline: none;
}

.nav a.active{
    background-color: white;
    box-shadow: 0 0 0 3px #fea;
    border-color: #fb1;
}

.nav a.active svg > *{
    fill: #fb1;
}

.navMobile{
    display: block;
    position: absolute;
    top: 65px;
    right: 0px;
    padding: 0 1rem;
    background-color: white;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
    border-radius: .2rem;
    transform: translateX(-10px);
    opacity: 0;
    pointer-events: none;
}

.navMobileActive{
    transition: .3s;
    transform: initial;
    opacity: 1;
    z-index: 100;
    pointer-events: initial;

}

.navMobile a , 
.navMobile button{
    display: flex;
    align-items: center;
    background-color: transparent;
    width: 100%;
    border: none;
    border-bottom: 1px solid #eee;
    padding: .5rem 0;
    cursor: pointer;
}
.navMobile a:hover svg > *, 
.navMobile button:hover svg > *{
    fill: #fb1;
}


.navMobile button{
    border-bottom:none;
}

.navMobile svg{
    margin-right: .7rem;
}





.btnMobile{
    background-color: #eee;
    border-radius: .2rem;
    height: 40px;
    width: 40px;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    border: solid 1px transparent;
    transition: .1s;
    cursor: pointer;
}

.btnMobile::after{
    content: '';
    display: block;
    width: 1.2rem;
    height: 2px;
    border-radius: 2px;
    background-color: currentColor;    
    box-shadow: 0 6px currentColor, 0 -6px currentColor;
    transition: .2s;
}

.btnMobile:focus, 
.btnMobile:hover{
    outline: none;
    background-color: white;
    box-shadow: 0 0 0 3px #fea;
    border-color: #fb1;
    color: #fb1;
}

.btnMobileActive{
    outline: none;
    background-color: white;
    box-shadow: 0 0 0 3px #fea;
    border-color: #fb1;
    color: #fb1;
}
.btnMobileActive::after{
    transform: rotate(-90deg);  
    width: 4px; 
    height: 4px; 
    box-shadow: 0 8px currentColor, 0 -8px currentColor;
    border-radius: 50%;
}