.form{
    display: grid;
    grid-template-columns: 1fr auto;
    align-items: stretch;
    margin: 1rem;
    padding: 8px 5px;
}

.single.form{
    margin: 1rem 0;
}

.textarea{
    display: block;
    width: 100%;
    font-size: 1rem;
    font-family: var(--type-firs);
    resize: none;
    border: 1px solid #eee;
    padding: .5rem;
    border-radius: .2rem;
    background: #eee;
    transition: .2s;
}

.textarea:focus,
.textarea:hover{
    outline: none;
    border-color: #fb1;
    background: white;
    box-shadow: 0 0 0 3px #fea;
}


.btnSend{
    cursor: pointer;
    color: #333;
    background: transparent;
    font-size: 1rem;
    padding: 0 1rem;
    overflow:hidden;
    border: none;
    box-shadow: 1px 1px 7px -2px #333;    
    border-radius: 4px;
    margin-left: 8px;
    transition: .3s;
    outline: none;
}

.btnSend svg path,
.btnSend svg path{
    fill: #fea;
    stroke: #fb1;
}

.btnSend:hover svg g,
.btnSend:focus svg g{
    fill: #fea;
    stroke: #fb1;
    animation: latir infinite .54s;
}

@keyframes latir{
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}

/*disssabled*/
.btnSend:disabled{
    cursor: default;
    box-shadow: none;    
    outline: none;
}

.btnSend:disabled svg path{
    fill: #333;
    stroke: none;
}
.btnSend:disabled:hover svg g{
    fill: #fea;
    stroke: #fb1;
    animation: latirX forwards;
}

@keyframes latirX{
    0%{
        opacity: 1;
    }
    100%{
        opacity: 1;
    }
}




