body{
    margin: 0;
    color: #333;    
    --type-first: Helvetica, Arial, sans-serif;
    --type-second: 'Spectral', georgia;
    font-family: var(--type-first);
    padding-top: 4rem;
}

*{
    box-sizing: border-box;
}

h1, h2, h3, h4, p{
    margin: 0px;
}

ul, li{
    list-style: none;
    padding: 0;
    margin: 0;
}

img{
    display: block;
    max-width: 100%;
}

a{
    text-decoration: none;
    color: #333;
}


button{
    cursor: pointer;
}
button,input{
    display: block;
    font-size: 1rem;
    font-family: var(--type-first);
    color: #333;
}

.container{
    max-width: 50rem;
    padding: 0 1rem;
    margin: 0 auto;
}

.mainContainer{
