}

.title{
    font-family: var(--type-second);
    line-height: 1;
    font-size: 3rem;
    margin: 1rem 0;
    position: relative;
    z-index: 1;
}



.title::after{
    content: '';
    display: block;
    width: 1.5rem;
    height: 1.5rem;
    background-color: #fb1;
    position: absolute;
    bottom: 5px;
    left: -5px;
    border-radius: .2rem;
    z-index: -1;
}

.animeLeft{
    opacity: 0;
    transform: translateX(-50px);
    animation: animeleft forwards .3s;
}

@keyframes animeleft{
    to{
        transform: translateX(0);
        opacity: 1;
    }
}

